.splitContent {
    justify-content: space-between;
    min-width: 50%;
}
.col {
    flex: 1 2 0;
    max-width: 100%;
}

@media (max-width: 991.98px) {
    .title {
        text-align: center;
   }
    .cardSect {
        justify-content: center;
        min-height: 100px;
   }
    #dividerWrap {
        flex-basis: 0;
        flex-grow: 1;
        min-width: 90%;
   }
   #dividerWrap2 {
    flex-basis: 0;
    flex-grow: 1;
    min-width: 90%;
}
    .item {
        display: block;
        min-width: 300;
   }
    .contentDivider {
        margin: 15px 0 15px 0;
        font-size: 20px;
   }
    .contentDivider .dividedText {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
            text-align: center;
   }
    .contentDivider .dividedText:before, .contentDivider .dividedText:after {
        content: '';
        border-top: 2px solid;
        margin: 0 20px 0 0;
        flex: 1 0 20px;
   }
    .contentDivider .dividedText:after {
        margin: 0 0 0 20px;
   }
}
@media (min-width: 992px) {
    #dividerWrap {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 20px;
        padding: -10px 0 -10px;
        justify-content: center;
   }
    .split {
        justify-content: center;
   }
    .contentDivider {
        position: relative;
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-content: center;
        justify-self: center;
        height: 100%;
        min-width: 100%;
   }
    .contentDivider .dividedText:before, .contentDivider .dividedText:after {
        position: absolute;
        content: "";
        width: 1px;
        left: 50%;
        border-left: 2px solid black;
   }
    .contentDivider .dividedText:before {
        bottom: 50%;
        top: 0;
        margin-bottom: 20px;
   }
    .contentDivider .dividedText:after {
        top: 50%;
        bottom: 0;
        margin-top: 20px;
   }
}

.button-background-move-menu {
    letter-spacing: 1px;
    padding: 35px 40px 12px;
    outline: 0;
    border: 2px solid #e3b505;
    cursor: pointer;
    position: relative;
    background-color: rgba(0, 0, 0, 0);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
  }
 
  .button-background-move-menu:after {
    content: "";
    border: 2px solid #e3b505;
    width: 100%;
    z-index: -1;
    position: absolute;
    height: 100%;
    top: 7px;
    left: 7px;
    transition: 0.5s;
  }
  
  .button-background-move-menu:hover:after {
    top: 0px;
    left: 0px;
  }